@import url(https://fonts.googleapis.com/css2?family=Domine:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abel&family=Courier+Prime:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{background:#FFE135}.btn__main{font-family:'Courier Prime', sans-serif;font-size:1.3rem;border:4px solid #000;border-radius:45px;padding:5px 37px;text-decoration:none;color:black}.btn__main:hover{transform:translateY(5px);padding:7px 40px;transform:translateY(0px);transition:0.2s}

.page-container{position:relative}.joke-container{position:absolute;top:50%;left:50%;margin:-300px;max-width:300px;background:transparent;transform:rotate(-13deg) translateX(50%);font-size:1.3rem}.joke-container p,.joke-container b{background:none;font-family:'Courier Prime', sans-serif;animation:pop 0.8s infinite}@keyframes pop{from{transform:scale(0.97)}50%{transform:scale(1)}to{transform:scale(0.97)}}.logo-container{display:flex;justify-content:center;align-items:center;flex-direction:column;height:100vh}

.logo svg{height:220px;margin-bottom:70px}

